    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 50%; }
    @media (max-width: 1149px) {
      .main-section::after {
        display: block; } }
  .main-section .container {
    position: relative;
    min-height: calc(100vh - 430px);
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .main-section .main-title {
    /* Только если меньше контейнера */
    font-size: 110px;
    line-height: 1.1;
    color: #FBF8F0;
    font-weight: 400;
    margin-bottom: 112px;
    z-index: 1; }
    @media (max-width: 1320px) {
      .main-section .main-title {
        font-size: calc(54px + 56 * ((100vw - calc(360px)) / 960)); } }
    @media (max-width: 479px) {
